
@import url("./assets/css/font-awesome.css");
@import url("./assets/css/animate.css");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700");
html {
  background-color: #141414;
  padding: 0px;
}
body {
  overflow-x: hidden;
  margin: 0;
}
router-view {
  width: 100%;
  height: 100vh;
}
#app {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  display: -ms-flexbox;
  display: flex;
}
#sidebar {
  position: fixed;
  left: 0;
  top: 0;
  width: 15vw;
  height: 100vh;
  z-index: 10;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
#sidebar:hover {
    background-color: #2E2E2E;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
#sidebar .name {
    width: 100%;
    color: white;
    font-weight: 200;
    line-height: 2.1em;
    padding: 15px;
    padding-top: 7vh;
}
@media (max-width: 1279px) {
#sidebar .name {
        line-height: 1.5em;
}
}
@media (max-width: 959px) {
#sidebar .name {
        line-height: 1.3em;
}
}
#sidebar .name .first {
      font-size: 2em;
}
@media (max-width: 1919px) {
#sidebar .name .first {
          font-size: 1.5em;
}
}
@media (max-width: 1279px) {
#sidebar .name .first {
          font-size: 1em;
}
}
@media (max-width: 959px) {
#sidebar .name .first {
          font-size: 1em;
}
}
#sidebar .name .last {
      font-size: 2.5em;
}
@media (max-width: 1919px) {
#sidebar .name .last {
          font-size: 2em;
}
}
@media (max-width: 1279px) {
#sidebar .name .last {
          font-size: 1.5em;
}
}
@media (max-width: 959px) {
#sidebar .name .last {
          font-size: 1.0em;
}
}
#sidebar .name .caption {
      font-size: .8em;
      padding-left: 10px;
}
@media (max-width: 959px) {
#sidebar .name .caption {
          font-size: .5em;
}
}
#sidebar .links {
    width: 100%;
    height: 500px;
    color: white;
}
#sidebar .links .link {
      text-align: center;
      font-size: 1.0em;
      font-weight: 200;
      width: 100%;
      height: 40px;
      line-height: 40px;
      transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
#sidebar .links .link i {
        position: absolute;
        left: 0;
        line-height: 40px;
        margin-left: 4vw;
}
@media (max-width: 1919px) {
#sidebar .links .link i {
            margin-left: 2vw;
}
}
@media (max-width: 959px) {
#sidebar .links .link i {
            display: none;
}
}
#sidebar .links .link:hover {
        background-color: #33F8FF;
        cursor: pointer;
        color: black;
}
@media (max-width: 599px) {
#sidebar {
      margin-left: -15vw;
      display: none;
}
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



